<template>
  <div id="foot">
    <div class="foot-content">
      <div class="foot-column left">
        <p>关于普信天宇</p>
        <div class="foot-item-wrapper">
          <div v-for="(item, index) in about" v-bind:key="index" class="foot-item">
            <p class="pointer left">{{item}}</p>
          </div>
        </div>
      </div>
      <div class="foot-column left">
        <p>网站地图</p>
        <div class="foot-item-wrapper">
          <div v-for="(item, index) in map" v-bind:key="index" class="foot-item">
            <p class="pointer left">{{item}}</p>
          </div>
        </div>
      </div>
      <div class="foot-column left">
        <p>快速链接</p>
        <div class="foot-item-wrapper">
          <div v-for="(item, index) in links" v-bind:key="index" class="foot-item">
            <a :href="item.url" target="_blank" class="pointer left">{{item.name}}</a>
          </div>
        </div>
      </div>
      <div class="we-chat">
        <p @click="see">官方微信</p>
        <transition name="icon">
          <div v-if="this.$root.adapt || QR" class="we-chat-icon"></div>
        </transition>
        <div v-if="!this.$root.adapt">
          <p>联系我们</p>
          <p>法律声明</p>
        </div>
      </div>
      <div class="foot-bar">
        <p class="left pointer">法律声明</p>
        <p class="left">|</p>
        <p class="left pointer">联系我们</p>
        <p class="right">版权所有&nbsp;&copy;&nbsp;{{renderDuration}} {{company.name}}.&nbsp;保留一切权利.</p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'foot',
  data () {
    return {
      about: [
        '公司介绍',
        '愿景与使命',
        '管理团队',
        '联系我们'
      ],
      map: [
        '产品与解决方案',
        '新闻中心',
        '技术支持',
        '合作伙伴'
      ],
      links: [
        {name: '华为', url: 'http://www.huawei.com/cn/?utm_source=bdpz&utm_campaign=regular&utm_medium=cpc'},
        {name: '新华三', url: 'http://www.h3c.com/'},
        {name: '安天', url: 'http://www.antiy.cn/'},
        {name: '思科', url: 'https://www.cisco.com/c/zh_cn/index.html'}
      ],
      QR: false,
      company: {
        name: '深圳市普信天宇电子科技有限公司',
        initialTime: 2009
      }
    }
  },
  methods: {
    see: function () {
      if (!this.$root.adapt) {
        this.QR = !this.QR
        if (this.QR) {
          this.$root.eventHub.$emit('prompt')
        }
      }
    }
  },
  computed: {
    renderDuration: function () {
      var date = new Date()
      return this.company.initialTime + '-' + date.getFullYear()
    }
  },
  mounted () {
    var self = this
    this.$root.eventHub.$on('cancelPrompt', function () {
      self.QR = false
    })
  }
}
</script>

<style scoped>
  #foot{
    width: 100%;
    background: #373D41;
  }
  .foot-content{
    overflow: hidden;
  }
  .foot-item-wrapper{
    width: 100%;
    height: auto;
  }

  @media only screen and (max-device-width: 767px) {
    #foot{
      height: 560px;
    }
    .foot-content{
      width: calc(100% - 40px);
      height: auto;
      margin: 0 20px;
    }
    .foot-column{
      width: 100%;
      height: 90px;
      padding: 24px 0 12px;
    }
    .foot-column>p:first-of-type{
      font-size: 16px;
      color: #A9A9A9;
      user-select: none;
    }
    .foot-item-wrapper{
      margin: 12px 0;
      overflow: hidden;
    }
    .foot-item{
      width: 50%;
      margin: 5px 0;
      float: left;
    }
    .foot-item p, .foot-item a{
      font-size: 16px;
      line-height: 26px;
      color: #C0C0C0;
    }
    .foot-item p:active, .foot-item a:active{
      text-decoration: none;
      color: #DCDCDC;
    }
    .we-chat{
      float: left;
      width: 100%;
      margin: 12px 0 0;
      position: relative;
    }
    .we-chat p{
      font-size: 16px;
      line-height: 26px;
      margin: 5px 0;
      color: #A9A9A9;
      user-select: none;
    }
    .we-chat-icon{
      width: 140px;
      height: 140px;
      background: url("../../resource/assets/QR.png") no-repeat;
      background-size: cover;
      position: fixed;
      top: calc(50% - 70px);
      left: calc(50% - 70px);
      z-index: 30;
    }
    .icon-enter-active{
      -webkit-transition: all .25s;
      -o-transition: all .25s;
      transition: all .25s;
    }
    .icon-enter{
      opacity: 0;
      -webkit-transform: translate3d(0, 80px, 0);
      -o-transform: translate3d(0, 80px, 0);
      transform: translate3d(0, 80px, 0);
    }
    .foot-bar{
      width: 100%;
      height: 60px;
      float: left;
      border-top: 1px solid #696969;
      margin: 10px 0 0;
    }
    .foot-bar .left{
      display: none;
    }
    .foot-bar .right{
      padding: 6px 0 0;
      font-size: 16px;
      line-height: 140%;
      color: #C0C0C0;
    }
  }

  @media only screen and (min-device-width: 768px) {
    #foot{
      height: 350px;
    }
    .foot-content{
      width: calc(100% - 40px);
      height: auto;
      margin: 0 20px;
    }
    .foot-column{
      width: calc((100% - 150px) / 3);
      padding: 50px 14px 30px 0;
      height: 200px;
    }
    .foot-column>p:first-of-type{
      font-size: 16px;
      color: #A9A9A9;
      user-select: none;
    }
    .foot-item-wrapper{
      margin: 20px 0;
    }
    .foot-item{
      width: 100%;
      margin: 5px 0;
      overflow: hidden;
    }
    .foot-item p, .foot-item a{
      font-size: 16px;
      line-height: 26px;
      color: #C0C0C0;
    }
    .foot-item p:hover, .foot-item a:hover{
      text-decoration: none;
      color: #DCDCDC;
    }
    .we-chat{
      float: right;
      margin: 50px 0;
    }
    .we-chat p{
      font-size: 16px;
      color: #A9A9A9;
      margin-bottom: 25px;
      user-select: none;
    }
    .we-chat-icon{
      width: 108px;
      height: 108px;
      background: url("../../resource/assets/QR.png") no-repeat;
      background-size: cover;
    }
    .foot-bar{
      width: 100%;
      height: 70px;
      float: left;
      border-top: 1px solid #696969;
    }
    .foot-bar p{
      font-size: 16px;
      line-height: 140%;
      color: #C0C0C0;
      user-select: none;
    }
    .foot-bar .left{
      margin: 8px 6px 0 0;
    }
    .foot-bar .left:not(:nth-of-type(2)):hover{
      color: #DCDCDC;
    }
    .foot-bar .right{
      margin: 8px 0 0 0;
      width: 300px;
      text-align: right;
    }
  }

  @media only screen and (min-device-width: 768px) and (min-width: 617px) {
    .foot-content{
      width: calc(100% - 100px);
      margin: 0 50px;
    }
    .foot-bar .right{
      width: auto;
      max-width: calc(100% - 200px);
    }
  }

  @media only screen and (min-device-width: 768px) and (min-width: 950px) {
    .foot-content{
      width: 850px;
      margin: 0 calc((100% - 850px) / 2);
    }
    .foot-column{
      width: 200px;
      padding: 50px 10px 30px 0;
    }
    .we-chat{
      margin: 50px;
    }
  }

  @media only screen and (min-device-width: 768px) and (min-width: 1294px) {
    .foot-content{
      width: 1066px;
      margin: 0 calc((100% - 1066px) / 2);
    }
    .foot-column{
      width: 240px;
      padding: 50px 30px 30px 0;
    }
  }

  @media only screen and (min-device-width: 768px) and (min-width: 1508px) {
    .foot-content{
      width: 1280px;
      margin: 0 calc((100% - 1280px) / 2);
    }
  }
</style>



